@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=block);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=block);
@charset "UTF-8";

:root {
    --scrollbar: #999999;
    --scrollbar-track: #cccccc;
}

html {
    scrollbar-face-color: #999999;
    scrollbar-face-color: var(--scrollbar);
    scrollbar-base-color: #999999;
    scrollbar-base-color: var(--scrollbar);
    scrollbar-3dlight-color: #999999;
    scrollbar-3dlight-color: var(--scrollbar);
    scrollbar-highlight-color: #999999;
    scrollbar-highlight-color: var(--scrollbar);
    scrollbar-track-color: #cccccc;
    scrollbar-track-color: var(--scrollbar-track);
    scrollbar-arrow-color: #cccccc;
    scrollbar-arrow-color: var(--scrollbar-track);
    scrollbar-shadow-color: #999999;
    scrollbar-shadow-color: var(--scrollbar);
    scrollbar-dark-shadow-color: #999999;
    scrollbar-dark-shadow-color: var(--scrollbar);
    scroll-behavior: smooth;
}

body {
    font-family: "Open Sans", sans-serif !important;
    color: #000000;
    background-color: #FFFFFF;
    overflow-y: scroll;
    overflow-x: hidden;
    touch-action: pan-x pan-y;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
    width: 8px;
    height: 3px;
}

::-webkit-scrollbar-track {
    background-color: #999999;
    background-color: var(--scrollbar);
}

::-webkit-scrollbar-track-piece {
    background-color: #cccccc;
    background-color: var(--scrollbar-track);
}

::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #999999;
    background-color: var(--scrollbar);
    border-radius: 3px;
}

::-webkit-scrollbar-corner {
    background-color: #999999;
    background-color: var(--scrollbar);
}

::-webkit-resizer {
    background-color: #999999;
    background-color: var(--scrollbar);
}

::selection {
    background-color: #007BFF;
    color: #fff;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif !important;
}

h1 {
    font-size: 2.80rem;
}
.not-draggable {
    user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -moz-user-select: none;
    -o-user-drag: none;
    -ms-user-select: none;
}

.ll-container {
    max-width: 430px;
    margin-top: 22px;
    margin-bottom: 45px;
}

@media screen and (prefers-color-scheme: dark) {
    :root {
        --scrollbar: #555555;
        --scrollbar-track: #292929;
    }

    body {
        color: #FFFFFF;
        background-color: #000000;
    }
}

@media screen and (max-width: 430px) {
    .ll-container {
        width: 100%;
    }
}

.ll-header {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}

.ll-header-text {
    font-size: 2.80rem;
}

.ll-social {
    background-size: cover;
    width: 37px;
    height: 37px;
    object-fit: cover;
    -webkit-filter: invert(43%) sepia(0%) saturate(0%) hue-rotate(190deg) brightness(90%) contrast(87%);
            filter: invert(43%) sepia(0%) saturate(0%) hue-rotate(190deg) brightness(90%) contrast(87%);
    cursor: pointer;
    transition: -webkit-filter 0.35s;
    transition: filter 0.35s;
    transition: filter 0.35s, -webkit-filter 0.35s;
}

.ll-social:hover {
    -webkit-filter: invert(18%) sepia(0%) saturate(1%) hue-rotate(103deg) brightness(93%) contrast(89%);
            filter: invert(18%) sepia(0%) saturate(1%) hue-rotate(103deg) brightness(93%) contrast(89%);
}

.profile-image {
    -webkit-filter: drop-shadow(0px 0px 12px #f2f2f2);
            filter: drop-shadow(0px 0px 12px #f2f2f2);
}

@media (prefers-color-scheme: dark) {
    .ll-social {
        -webkit-filter: invert(100%) sepia(2%) saturate(1392%) hue-rotate(196deg) brightness(120%) contrast(84%);
                filter: invert(100%) sepia(2%) saturate(1392%) hue-rotate(196deg) brightness(120%) contrast(84%);
        transition: -webkit-filter 0.25s;
        transition: filter 0.25s;
        transition: filter 0.25s, -webkit-filter 0.25s;
    }

    .ll-social:hover {
        -webkit-filter: invert(61%) sepia(19%) saturate(0%) hue-rotate(167deg) brightness(108%) contrast(91%);
                filter: invert(61%) sepia(19%) saturate(0%) hue-rotate(167deg) brightness(108%) contrast(91%);
    }

    @media screen and (prefers-color-scheme: dark) {
        .profile-image {
            -webkit-filter: initial;
                    filter: initial;
        }
    }
}
